body {
  font: 18px "Century Gothic", Futura, sans-serif;
  margin: 2em;
  background: -webkit-repeating-linear-gradient(
  315deg,
  #4C0F54,
  #4C0F54 15px,
  #310138 15px,
  #310138 30px
);
  background: repeating-linear-gradient(
  135deg,
  #4C0F54,
  #4C0F54 15px,
  #310138 15px,
  #310138 30px
);
  color: #fff;
  height: calc(100vh - 4em);
}

h2 {
  text-align: center;
}

ol, ul {
  padding-left: 0px;
  list-style: none;
}

.status {
  margin-bottom: 1em;
}

.board-grid {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  -webkit-justify-content: center;
          justify-content: center;
  max-width: 900px;
  margin: 0 auto;
  -webkit-align-items: center;
          align-items: center;
}

.board-grid > .square::before {
  content: "";
  display: inline-block;
  width: 1px;
  height: 0;
  padding-bottom: calc(100% / 1);
}

.square {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  background: rgba(0,0,0,0.5);
  border: 1px solid rgba(255,255,255,0.75);
  font-size: 10vw;
  font-weight: bold;
  padding: 0;
  text-align: center;
  height: 100%;
  color: #fff;
}

.square:focus {
  outline: none;
}

.kbd-navigation .square:focus {
  background: #ddd;
}

.game {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

.game-board {
  -webkit-flex: 0 0 60%;
          flex: 0 0 60%;
}

.game-info {
  -webkit-flex: 0 0 30%;
          flex: 0 0 30%;
  padding: 1em;
  background: rgba(0,0,0,0.5);
}

.game-player {
  -webkit-flex: 0 0 100%;
          flex: 0 0 100%;
  text-align: center;
  font-size: 5vw;
  margin-bottom:1em;
}

.btn {
    letter-spacing: 0;
  color: #fff;
  cursor: pointer;
  font-size:16px;
  font-weight: 400;
  line-height: 45px;
  margin: 0 0 0.5em;
  position: relative;
  text-decoration: none;
  text-transform: uppercase;
  width: 100%;
  background: transparent;
  border: 0;
}

.btn:hover,
.btn:active {
  letter-spacing: 3px;
}

.btn:after,
.btn:before {
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  border: 1px solid rgba(#fff, 0);
  bottom: 0px;
  content: " ";
  display: block;
  margin: 0 auto;
  position: relative;
  -webkit-transition: all 280ms ease-in-out;
  transition: all 280ms ease-in-out;
  width: 0;
}

.btn:hover:after,
.btn:hover:before {
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  border-color: #fff;
  -webkit-transition: width 350ms ease-in-out;
  transition: width 350ms ease-in-out;
  width: 70%;
}

.btn:hover:before {
  bottom: auto;
  top: 0;
  width: 70%;
}

canvas {
  display: -webkit-flex;
  display: flex;
  position: absolute;
  width: 100vw;
  height: 100vh;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

@media (max-width: 768px) {
  .square {
    height: 30vw;
    width: 30vw;
  }
  .game {
    -webkit-flex-direction: column;
            flex-direction: column;
  }
  .game-info {
    margin-left: 0;
    margin-top: 2em;
    -webkit-align-self: center;
            align-self: center;
  }
}

